// Constants for localStorage keys
const ENDPOINT_STORAGE_KEY = 'powerAutoMateEndpoint';
const DEFAULT_ENDPOINT = '';
// https://prod-145.westus.logic.azure.com:443/workflows/3cb2e5271477448496e7671a8165d8d4/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=beB6t96b4-0l85jsHfwbiVbgrMnwGYZDNmYAV3wcOyk

// Wait for the DOM to be fully loaded before attaching event listeners
document.addEventListener('DOMContentLoaded', () => {
    // UI Elements
    const settingsToggle = document.getElementById('settingsToggle');
    const settingsPanel = document.getElementById('settingsPanel');
    const endpointInput = document.getElementById('endpointInput');
    const saveSettingsButton = document.getElementById('saveSettings');
    const endpointErrorMsg = document.getElementById('endpointErrorMsg');

    // Ensure all elements exist before attaching event listeners
    if (settingsToggle && settingsPanel) {
        // Add settings panel toggle functionality
        settingsToggle.addEventListener('click', () => {
            settingsPanel.classList.toggle('open');
            console.log('Settings toggle clicked'); // Debug log
        });
    } else {
        console.error('Settings toggle or panel elements not found');
    }

    // Validate URL function
    function isValidUrl(url) {
        try {
            new URL(url);
            return true;
        } catch {
            return false;
        }
    }

    // Load saved endpoint on initialization
    function loadSavedEndpoint() {
        const savedEndpoint = localStorage.getItem(ENDPOINT_STORAGE_KEY) || DEFAULT_ENDPOINT;
        if (endpointInput) {
            endpointInput.value = savedEndpoint;
        }
    }

    // Save settings functionality
    if (saveSettingsButton) {
        saveSettingsButton.addEventListener('click', () => {
            const newEndpoint = endpointInput.value.trim();

            if (!isValidUrl(newEndpoint)) {
                if (endpointErrorMsg) {
                    endpointErrorMsg.textContent = 'Please enter a valid URL';
                }
                return;
            }

            try {
                localStorage.setItem(ENDPOINT_STORAGE_KEY, newEndpoint);
                
                if (endpointErrorMsg) {
                    endpointErrorMsg.textContent = '';
                }
                
                if (settingsPanel) {
                    settingsPanel.classList.remove('open');
                }
                
                // Optional: Show a success message or toast
                const submitMessage = document.getElementById('submitMessage');
                if (submitMessage) {
                    submitMessage.textContent = 'Endpoint saved successfully!';
                    submitMessage.className = 'message success';
                    submitMessage.style.display = 'block';
                    setTimeout(() => {
                        submitMessage.style.display = 'none';
                    }, 3000);

                    loadProjectIds();
                    loadDateTypes();
                }
            } catch (error) {
                if (endpointErrorMsg) {
                    endpointErrorMsg.textContent = 'Unable to save settings. Check browser storage permissions.';
                }
                console.error('Error saving endpoint:', error);
            }
        });
    }

    // Load saved endpoint
    loadSavedEndpoint();
});

// Modify existing fetch calls to use saved/default endpoint
async function getEndpoint() {
    return localStorage.getItem(ENDPOINT_STORAGE_KEY) || DEFAULT_ENDPOINT;
}

// Existing Office.js initialization and other functions remain the same
Office.onReady((info) => {
    if (info.host === Office.HostType.Outlook) {
        const submitButton = document.getElementById('submit');
        if (submitButton) {
            submitButton.onclick = submitForm;
        }
        loadProjectIds();
        loadDateTypes();
    }
});

async function loadProjectIds() {
    const dropdown = document.getElementById('projectIdDropdown');
    const loader = document.getElementById('projectIdLoader');
    
    try {
        const endpoint = await getEndpoint();

        if(endpoint.length == 0) 
            return;
        
        loader.style.display = 'block';
        dropdown.disabled = true;

        const response = await fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ type: 'get-project-ids' })
        });

        await new Promise(resolve => setTimeout(resolve, 500));

        const data = await response.json();
        
        dropdown.innerHTML = '';
        
        const defaultOption = document.createElement('option');
        defaultOption.text = 'Select Project ID';
        defaultOption.value = '';
        dropdown.appendChild(defaultOption);

        data.projectIds.forEach(id => {
            const option = document.createElement('option');
            option.value = id;
            option.text = id;
            dropdown.appendChild(option);
        });

        loader.style.display = 'none';
        dropdown.disabled = false;
    } catch (error) {
        console.error('Error loading project IDs:', error);
        
        loader.style.display = 'none';
        dropdown.disabled = true;
        dropdown.innerHTML = '<option>Failed to load Project IDs</option>';
    }
}

