async function submitForm() {
    const projectIdDropdown = document.getElementById('projectIdDropdown');
    const dateTypeDropdown = document.getElementById('dateTypeDropdown');
    const dateSelector = document.getElementById('dateSelector');
    const submitLoader = document.getElementById('submitLoader');
    const submitMessage = document.getElementById('submitMessage');
    const submitButton = document.getElementById('submit');

    // Validation checks remain the same
    if (!projectIdDropdown.value) {
        submitMessage.textContent = 'Please select a Project ID';
        submitMessage.className = 'message error';
        submitMessage.style.display = 'block';
        return;
    }

    if (!dateTypeDropdown.value) {
        submitMessage.textContent = 'Please select a Date Type';
        submitMessage.className = 'message error';
        submitMessage.style.display = 'block';
        return;
    }

    if (!dateSelector.value) {
        submitMessage.textContent = 'Please select a Date';
        submitMessage.className = 'message error';
        submitMessage.style.display = 'block';
        return;
    }

    submitMessage.textContent = '';
    submitMessage.style.display = 'none';

    try {
        submitButton.disabled = true;
        submitLoader.style.display = 'block';

        const endpoint = await getEndpoint();
        const response = await fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                type: 'submit-data',
                projectId: projectIdDropdown.value,
                dateType: dateTypeDropdown.value,
                date: dateSelector.value
            })
        });

        await new Promise(resolve => setTimeout(resolve, 1000));

        if (response.ok) {
            submitMessage.textContent = 'Data submitted successfully!';
            submitMessage.className = 'message success';
            submitMessage.style.display = 'block';
            
            // Reset form
            projectIdDropdown.selectedIndex = 0;
            dateTypeDropdown.selectedIndex = 0;
            dateSelector.value = '';
        } else {
            const errorData = await response.json().catch(() => ({}));
            throw new Error(errorData.message || 'Submission failed');
        }
    } catch (error) {
        console.error('Error submitting form:', error);
        
        submitMessage.textContent = error.message || 'Error submitting data. Please try again.';
        submitMessage.className = 'message error';
        submitMessage.style.display = 'block';
    } finally {
        submitLoader.style.display = 'none';
        submitButton.disabled = false;
    }
}